<template>
    <div>
        <el-button @click="dialogVisible = true" size="mini">选择顾问</el-button>
        <el-dialog  top="10px"  :visible.sync="dialogVisible"  width="80%"  title="选择顾问" :modal-append-to-body="false">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" label-width="100" class="search-form"  :inline="true">
                    <el-form-item  label="关键字(用户昵称/手机号)" >
                        <el-input v-model="searchForm.keywords"></el-input>
                    </el-form-item>
                    <el-form-item  label="用户ID" >
                        <el-input v-model="searchForm.user_id"></el-input>
                    </el-form-item>
                    <el-form-item  label="顾问等级">
                        <el-select v-model="searchForm.agent_type" placeholder="请选择">
                            <el-option label="请选择" :value=0></el-option>
                            <el-option label="普通顾问" :value=1></el-option>
                            <el-option label="资深顾问" :value=2></el-option>
                            <el-option label="专家顾问" :value=3></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary">搜索</el-button>
                    </el-form-item>
                </el-form>
                <el-table size="mini" :data="datas.list" height="500" style="width: 100%" >
                    <el-table-column prop="user_id" label="用户ID">
                        <template slot-scope="scope">
                            <p>用户ID：<span>{{scope.row.user_id}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="用户昵称" >
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.nickname}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="头像">
                        <template slot-scope="scope">
                            <img width="40" :src="scope.row.face"/>
                        </template>
                    </el-table-column>

                    <el-table-column prop="mobile" label="手机号码" ></el-table-column>

                    <el-table-column label="顾问等级" >
                        <template slot-scope="scope">
                            {{scope.row.agent_group_mean}}
                        </template>
                    </el-table-column>

                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button type="text" @click="puserSelect(scope.row)" >选择</el-button>
                        </template>
                    </el-table-column>

                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from "@/api";
    import DateBetween from "../../../components/DateBetween";


    export default {
        name: 'AgentSelect',
        components: {DateBetween},
        props:[],
        data() {
            return {
                dialogVisible:false,
                //整体菊花
                loading: false,
                //列表数据
                datas: [],
                //搜索数据
                searchForm: this.initSearch(),
            }
        },
        watch:{
        },
        created() {
            this.getList();
        },
        methods: {
            initSearch(){
                return {
                    date:[],
                    keywords:'',
                    user_id:'',
                    agent_type: 0,
                }
            },

            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.agent.agent/getAgentList", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    end_add_time: this.searchForm.date[1] ? this.searchForm.date[1] :  0,
                    bg_add_time: this.searchForm.date[0] ? this.searchForm.date[0] : 0,
                    keywords: this.searchForm.keywords,
                    user_id: this.searchForm.user_id,
                    agent_type: this.searchForm.agent_type,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


            puserSelect(val){
                this.dialogVisible = false;
                this.$emit("puserSelect",val);
            },
        }
    }
</script>

