<template>
    <!--获取-->
    <div >
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column prop="goods_id" label="商品ID" width="80"></el-table-column>
                <el-table-column  label="商品图片" width="100">
                    <template slot-scope="scope">
                        <img width="50" :src="scope.row.goods_photo"/>
                    </template>
                </el-table-column>
                <el-table-column prop="goods_name" label="商品名称"></el-table-column>
                <el-table-column prop="goods_sn" label="商品编号"></el-table-column>
                <el-table-column prop="spec_key_name" label="规格"></el-table-column>
                <el-table-column prop="add_time" label="加入时间"></el-table-column>

                <el-table-column  label="移除">
                    <template slot-scope="scope">
                        <el-tag size="mini" type="info" v-if="scope.row.is_delete == 1">移除</el-tag>
                        <el-tag size="mini" v-if="scope.row.is_delete == 0" >未移除</el-tag>
                        <p v-if="scope.row.is_delete == 1">移除时间：{{scope.row.delete_time}}</p>
                    </template>
                </el-table-column>
                <el-table-column  label="购买">
                    <template slot-scope="scope">
                        <el-tag size="mini" type="info" v-if="scope.row.is_pay == 1">已支付</el-tag>
                        <el-tag size="mini" v-if="scope.row.is_pay == 0" >未支付</el-tag>
                        <p v-if="scope.row.is_pay == 1">购买时间：{{scope.row.pay_time}}</p>
                    </template>
                </el-table-column>

            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "UserGoodsCart",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.user.userview/cart", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
<style scoped>

</style>
