<template>
    <div>
        <top-nav title="跟踪用户"></top-nav>
        <div class="wyu-main" v-loading="loading.loading">
            <div style="min-width: 1600px">
                <div class="wyu-block">
                    <el-row :gutter="10" style="height: 260px;padding: 20px">
                        <el-col :span="8">
                            <div class="user-view-item" style="height: 220px;padding: 50px 40px 40px 40px">
                                <el-col :span="8">
                                    <img width="120" height="120" :src="userDetail.face">
                                </el-col>
                                <el-col :span="8">
                                    <p style="color: #333333;font-weight:500;">
                                        {{userDetail.nickname}}(ID:{{$route.params.id}})</p>
                                    <div class="user-view-item-label"><span>{{userDetail.mobile}}</span></div>
                                    <div class="user-view-item-label"><span>性别：{{userDetail.sex_mean}}</span></div>
                                    <div class="user-view-item-label"><span>生日：{{userDetail.birthday}}</span></div>
                                </el-col>
                                <el-col :span="8"
                                        style="height: 100%; justify-content: flex-end; display: flex; align-items: center;">
                                    <el-button size="mini" @click="dialog.userDialog=true">操作</el-button>
                                </el-col>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span>用户余额</span>
                                    <el-dropdown style="float: right">
                                    <span class="el-dropdown-link user-view-item-title-btn">操作<i
                                         class="el-icon-arrow-down el-icon--right"></i>
                                    </span>
                                    <el-dropdown-menu split-button slot="dropdown" size="mini">
                                            <el-dropdown-item><span @click="dialog.moneyDialog = true">余额</span>
                                            </el-dropdown-item>
                                            <el-dropdown-item><span @click="dialog.jifenDialog = true">积分</span>
                                            </el-dropdown-item>
                                        </el-dropdown-menu>
                                    </el-dropdown>
                                </div>
                                <div class="user-view-item-label"><span>余额：</span><span style="color: red">￥：{{userDetail.money}}</span>
                                </div>
                                <div class="user-view-item-label"><span>积分：</span><span style="color: red">{{userDetail.integral}}</span>
                                </div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span>PLUS会员</span><span
                                        class="user-view-item-title-btn" @click="dialog.vipDialog = true">操作</span>
                                </div>
                                <div class="user-view-item-label"><span>是否开通：</span>
                                    <el-tag size="mini" type="info" v-if="userDetail.is_plus_vip == 0">未开通</el-tag>
                                    <el-tag size="mini" type="success" v-if="userDetail.is_plus_vip == 1">已开通</el-tag>
                                </div>
                                <div class="user-view-item-label"><span>到期时间：</span>
                                    <span v-if="userDetail.is_plus_vip == 1">{{userDetail.plus_vip_time}}</span>
                                    <span v-if="userDetail.is_plus_vip == 0">--</span>
                                </div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span>顾问身份</span><span
                                        class="user-view-item-title-btn" v-if="userDetail.is_agent == 0" @click="dialog.agentDialog=true">操作</span></div>
                                <div class="user-view-item-label"><span>顾问：</span>
                                    <el-tag size="mini" type="success" v-if="userDetail.is_agent == 1">是</el-tag>
                                    <el-tag size="mini" type="info" v-if="userDetail.is_agent == 0">否</el-tag>
                                </div>
                                <div class="user-view-item-label"><span>分&nbsp;&nbsp;&nbsp;组：</span><span>{{userDetail.agent_group_mean}}</span>
                                </div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span>推广人</span></div>
                                <div class="user-view-item-label"><span>直接推广者：</span><span
                                        style="display: inline-block;max-width: 130px" class="ellipsis-01">{{userDetail.pid_name}}</span><span
                                        v-if="userDetail.pid > 0">（ID:{{userDetail.pid}}）</span>
                                    <el-button v-if="userDetail.pid == 0" size="mini" @click="dialog.puserDialog = true">添加</el-button>
                                </div>
                                <div class="user-view-item-label"><span>推广者父级：</span><span
                                        style="display: inline-block;max-width: 130px" class="ellipsis-01">{{userDetail.pid2_name}}</span><span
                                        v-if="userDetail.pid > 0">（ID:{{userDetail.pid2}}）</span></div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span>订单信息</span><span
                                        class="user-view-item-title-btn" @click="dialog.couponDialog = true">赠送优惠券</span></div>
                                <div class="user-view-item-label"><span>订单数：</span><span>{{userDetail.order_num}}</span>
                                </div>
                                <div class="user-view-item-label">
                                    <span>优惠券：</span><span>{{userDetail.coupon_num}}</span></div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span>锁定信息</span></div>
                                <div class="user-view-item-label"><span>锁定状态：</span>{{userDetail.is_lock == 1 ? '锁定中' :
                                    '正常'}}<span style="margin-left: 10px">
                                    <el-switch @change="lockUser" v-loading="loading.lockLoading"
                                               v-model="userDetail.is_lock" :active-value="1"
                                               :inactive-value="0"></el-switch>
                                </span>
                                </div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span>注册信息</span></div>
                                <div class="user-view-item-label"><span>注册时间：</span><span>{{userDetail.add_time}}</span>
                                </div>
                                <div class="user-view-item-label"><span>注册设备：</span><span>{{userDetail.device}}</span>
                                </div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span>上次登录</span></div>
                                <div class="user-view-item-label">
                                    <span>登录时间：</span><span>{{userDetail.last_time}}</span></div>
                                <div class="user-view-item-label">
                                    <span>登录设备：</span><span>{{userDetail.last_device}}</span></div>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </div>
            <div class="wyu-block" style="margin-top: 20px">
                <user-view-log :user_id="$route.params.id"></user-view-log>
            </div>
        </div>
        <!--基本信息-->
        <el-dialog title="用户基本信息" :modal="false" :visible.sync="dialog.userDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.userInfoLoading">
                <el-form-item label="头像">
                    <img-uploads w="100" h="100" :img="userDetail.face" v-model="userDetail.face"></img-uploads>
                </el-form-item>
                <el-form-item label="手机号">
                    <el-input v-model="userDetail.mobile"></el-input>
                </el-form-item>
                <el-form-item label="昵称">
                    <el-input v-model="userDetail.nickname"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-select v-model="userDetail.sex">
                        <el-option label="保密" :value="0"></el-option>
                        <el-option label="男" :value="1"></el-option>
                        <el-option label="女" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="生日">
                    <el-date-picker
                            v-model="userDetail.birthday"
                            value-format="yyyy/MM/dd"
                            type="date"
                            placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.userDialog = false;getDetail()">取 消</el-button>
                    <el-button size="mini" type="primary" @click="editUserIfo">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!--用户余额-->
        <el-dialog title="账户余额" :visible.sync="dialog.moneyDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.moneyLoading">
                <el-form-item label="当前结余">
                    <span style="color: red">¥{{userDetail.money}}</span>
                </el-form-item>
                <el-form-item label="操作">
                    <el-select v-model="moneyForm.type" placeholder="选择会员组">
                        <el-option v-for="(val,key) in status.money_status" :key="key" :label="val.value"
                                   :value="val.key"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="操作余额">
                    <el-input v-model="moneyForm.money"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.moneyDialog = false;moneyForm=initMoneyForm()">取 消</el-button>
                    <el-button size="mini" type="primary" @click="editUserMoney">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!--用户积分-->
        <el-dialog title="账户积分" :visible.sync="dialog.jifenDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.jifenLoading">
                <el-form-item label="当前结余">
                    <span style="color: red">{{userDetail.integral}}</span>
                </el-form-item>
                <el-form-item label="操作">
                    <el-select v-model="jifenForm.type" placeholder="选择会员组">
                        <el-option v-for="(val,key) in status.integral_status" :key="key" :label="val.value"
                                   :value="val.key"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="操作积分">
                    <el-input v-model="jifenForm.integral"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.jifenDialog = false;jifenForm=initJifenForm()">取 消</el-button>
                    <el-button size="mini" type="primary" @click="editJIfenMoney">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!--plus会员-->
        <el-dialog title="PLUS会员" :visible.sync="dialog.vipDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.jifenLoading">

                <el-form-item label="开通会员">
                    <el-switch v-model="vipForm.is_vip" :inactive-value="0" :active-value="1"></el-switch>
                </el-form-item>

                <el-form-item label="会员到期" v-if="vipForm.is_vip == 1">
                    <el-date-picker
                            v-model="vipForm.vip_time"
                            value-format="yyyy/MM/dd"
                            type="date"
                            placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>

                <el-form-item>
                    <el-button size="mini" @click="dialog.vipDialog = false;vipForm=initVipForm()">取 消</el-button>
                    <el-button size="mini" type="primary" @click="editVip">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!--顾问身份-->
        <el-dialog title="顾问身份" :visible.sync="dialog.agentDialog" :modal-append-to-body="false" width="500px" center>
            <div class="wyu-tips wyu-tips-info" style="margin-bottom: 20px">
                这里只负责快速成文顾问 具体操作请 前往 顾问管理
            </div>
            <div class="wyu-tips wyu-tips-info" >
                确认 立即成文顾问吗？
            </div>
            <span slot="footer" class="dialog-footer">
                      <el-button size="mini" @click="dialog.agentDialog = false;">取 消</el-button>
                      <el-button size="mini" type="primary" @click="agent">确 定</el-button>
            </span>
        </el-dialog>
        <!--赠送优惠券-->
        <el-dialog title="赠送优惠券" :visible.sync="dialog.couponDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form label-width="100px" style="padding: 0" v-loading="loading.couponLoading">
                <el-form-item label="选择优惠券" >
                    <coupon-select @selCoupon="selCoupon"></coupon-select>
                </el-form-item>
                <el-form-item label="优惠券名称 " v-if="coupon">
                    {{coupon.name}}
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.couponDialog = false;loading.couponLoading = false;">取 消</el-button>
                    <el-button size="mini" type="primary" @click="sendCoupon()">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!--指定上级-->
        <el-dialog title="指定上级" :visible.sync="dialog.puserDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form label-width="100px" style="padding: 0" v-loading="loading.puserLoading">
                <el-form-item label="选择上级" >
                    <agent-select  @puserSelect="puserSelect"></agent-select>
                </el-form-item>
                <el-form-item label="上级信息" v-if="puser">
                    {{puser.nickname}} {{puser.user_id ? '(用户ID：'+ puser.user_id + ')' : ''}}
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.puserDialog = false;loading.puserLoading = false;">取 消</el-button>
                    <el-button size="mini" type="primary" @click="setPuser()">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    import ImgUploads from "../../components/Photos";
    import UserViewLog from "./components/UserViewLog";
    import CouponSelect from "./components/Coupon";
    import AgentSelect from "./components/AgentSelect";

    export default {
        name: "travelDepart",
        components: {AgentSelect, CouponSelect, UserViewLog, ImgUploads, TopNav,},
        data: function () {
            return {
                loading: {
                    loading: false, //整体菊花
                    userInfoLoading: false,
                    moneyLoading: false,
                    jifenLoading: false,
                    lockLoading: false,
                    vipLoading: false,
                    agentLoading: false,
                    couponLoading: false,
                    puserLoading: false,
                },
                //弹出控制
                dialog: {
                    userDialog: false,//用户基本信息修改
                    moneyDialog: false,//用户余额操作
                    jifenDialog: false,
                    vipDialog: false,
                    agentDialog: false,
                    couponDialog: false,
                    puserDialog: false,
                },
                birthdaybm: false,  //生日保密
                userDetail: {},
                status: {},//所有状态集合
                //余额form
                moneyForm: this.initMoneyForm(),
                jifenForm: this.initJifenForm(),
                vipForm: this.initVipForm(),
                couponForm: {},
                coupon: {},
                puser: {},
            };
        },
        created() {
            this.getDetail();
        },
        methods: {
            //初始化moneyform
            initVipForm() {
                return {
                    is_vip: 1,
                    vip_time: '2019/01/01',
                }
            },
            //初始化moneyform
            initMoneyForm() {
                return {
                    type: 3,
                    money: 0,
                }
            },
            //初始化moneyform
            initJifenForm() {
                return {
                    type: 5,
                    integral: 0,
                }
            },
            //锁定用户
            agent() {
                this.loading.agentLoading = true;
                api.post("/shop/admin.user.user/agent", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                }).then(() => {
                    this.dialog.agentDialog = false;
                    this.loading.agentLoading = false;
                    this.getDetail();
                }).catch(() => {
                    this.dialog.agentDialog = false;
                    this.loading.agentLoading = false;
                });
            },
            //锁定用户
            lockUser() {
                this.loading.lockLoading = true;
                api.post("/shop/admin.user.user/lock", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                }).then(() => {
                    this.loading.lockLoading = false;
                }).catch(() => {
                    this.loading.lockLoading = false;
                });
            },
            //修改操作余额
            editVip() {
                this.loading.vipLoading = true;
                api.post("/shop/admin.user.user/vip", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    is_vip: this.vipForm.is_vip,
                    vip_time: this.vipForm.vip_time,
                }).then(() => {
                    this.vipForm = this.initVipForm();
                    this.dialog.vipDialog = false;
                    this.loading.vipLoading = false;
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.vipLoading = false;
                });
            },
            //修改操作余额
            editJIfenMoney() {
                this.loading.jifenLoading = true;
                api.post("/shop/admin.user.user/jifen", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    type: this.jifenForm.type,
                    integral: this.jifenForm.integral,
                }).then(() => {
                    this.jifenForm = this.initJifenForm();
                    this.dialog.jifenDialog = false;
                    this.loading.jifenLoading = false;
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.jifenLoading = false;
                });
            },
            //修改操作余额
            editUserMoney() {
                this.loading.moneyLoading = true;
                api.post("/shop/admin.user.user/money", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    type: this.moneyForm.type,
                    money: this.moneyForm.money,
                }).then(() => {
                    this.moneyForm = this.initMoneyForm();
                    this.dialog.moneyDialog = false;
                    this.loading.moneyLoading = false;
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.moneyLoading = false;
                });
            },
            //修改用户基本信息
            editUserIfo() {
                this.loading.userInfoLoading = true;
                api.post("/shop/admin.user.user/editInfo", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    mobile: this.userDetail.mobile,
                    face: this.userDetail.face,
                    sex: this.userDetail.sex,
                    birthday: this.userDetail.birthday,
                    nickname: this.userDetail.nickname,
                }).then(() => {
                    this.loading.userInfoLoading = false;
                    this.dialog.userDialog = false;
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.userInfoLoading = false;
                });
            },

            //获取详情
            getDetail() {
                var that = this;
                that.loading.loading = true;
                api.post('shop/admin.user.user/detail', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                }).then(res => {
                    that.loading.loading = false;
                    that.userDetail = res.detail;
                    this.status = res.status;
                }).catch(() => {
                    that.loading.loading = false;
                });
            },


            sendCoupon() {
                var that = this;
                that.loading.couponLoading = true;
                api.post('shop/admin.marketing.coupon/sendCoupon', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                    coupon_id: that.coupon.coupon_id,
                }).then(res => {
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    that.loading.couponLoading = false;
                    that.coupon = {};
                    that.dialog.couponDialog = false;
                }).catch(() => {
                    that.loading.couponLoading = false;
                });
            },

            setPuser() {
                var that = this;
                that.loading.puserLoading = true;
                api.post('shop/admin.user.user/setpuser', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                    pid: that.puser.user_id,
                }).then(res => {
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    that.loading.puserLoading = false;
                    that.puser = {};
                    that.dialog.puserDialog = false;
                    this.getDetail();
                }).catch(() => {
                    that.loading.puserLoading = false;
                });
            },


            //
            selCoupon(val) {
                this.loading.couponLoading = false;
                this.coupon = val;
            },

            //
            puserSelect(val) {
                this.loading.puserLoading = false;
                this.puser = val;

            }

        }
    };
</script>
<style>
    .user-view-item {
        height: 100px;
        padding-top: 15px;
        padding-left: 20px;
        background-color: #F8F9FB;
    }

    .user-view-item-title {
        font-size: 14px;
        font-weight: 500;
        color: #333333;
    }

    .user-view-item-label {
        font-size: 12px;
        margin-top: 12px;
    }

    .user-view-item-label span:nth-child(1) {
        color: #888888;
    }

    .user-view-item-title-btn {
        color: #005DE5;
        float: right;
        margin-right: 20px;
        cursor: pointer
    }
</style>
